      select: [],
      effect:{
          key1:[],
          key2:[],
          key3:[],
          key4:[],
      },
      text: [],
      search: "",
      headers: [
        {
          text: "",
          align: "start",
          sortable: false,
          value: "chinese",
        },
        { text: "", value: "faction" },
        { text: "", value: "initialrarity" },
        { text: "", value: "attributes" },
        { text: "", value: "class" },
        { text: "", value: "attackMethod" },
        { text: "", value: "attackDirection" },
        { text: "HP", value: "hp" },
        { text: "", value: "dexterity" },
        { text: "", value: "physicalAttack" },
        { text: "", value: "powerAttack" },
        { text: "", value: "physicalDefense" },
        { text: "", value: "powerDefense" },
        // { text: "", value: "obtain" },
        // { text: "", value: "skill1Effect" },
        // { text: "", value: "nirvanaEffect" },
        // { text: "1", value: "potentialAbility1Effect" },
        // { text: "2", value: "potentialAbility2Effect" },
      ],
    };
  },
  components:{
    infoDialog
  },
  created: function () {
    this.card = card;
    this.getColor("");
  },
  methods: {
    getColor(attributes) {
      if (attributes == "") return "red";
      else if (attributes == "") return "blue";
      else return "green";
    },
    // (,)
    searchKeysValues(lists, filters) {
      let resArr = [];
      lists.filter((item) => {
        let flag = 1;
        for (let i in filters) {
          if (!item[i].includes(filters[i])) {
            flag = 0;
          }
        }
        if (flag === 1) {
          resArr.push(item);
        }
      });
      this.card = resArr;
    },
    // 
    deleteEmptyKey(obj) {
      let keys = {};
      for (let key in obj) {
        if (obj[key] && obj[key] != "") {
          keys[key] = obj[key];
        }
      }
      return keys;
    },
    handleClick(value){
      this.showInfoDialog=true;
      this.inputCard=value;
    },
    // 
    searchData() {
      let keys = this.deleteEmptyKey(this.cardSearch);
      if (this.cardSearch.attackDirection.length > 0) {
        keys.attackDirection = "";
        for (let i = 0; i < this.cardSearch.attackDirection.length; i++) {
          keys.attackDirection += this.cardSearch.attackDirection[i];
        }
        console.log(this.cardSearch.attackDirection, keys.attackDirection);
      }
      this.searchKeysValues(card, keys);
    },
  },
};
</script>